

@media (max-width: 1000px) {

    footer {
        
        .footer-cont {
            padding: 90px 30px;
            margin-left: 150px;
            flex-direction: column;
            
            .footer-1 {
                font-size: 24px;
            }
            
            .footer-1,
            .footer-2,
            .footer-3 {
                margin: 0 0 40px 0;
                width: 100%;

                p {
                    margin: 0;
                }
            }
        }
    }

}





@media (max-width: 800px) {

    footer {
        .footer-cont {
            margin-left: 0;
        }
    }

    nav {
        display: none;
    }

    .nav-mobile-button {
        display: block;
        position: fixed;
        top: 30px;
        right: 40px;

        padding: 0 20px 0 0;
        color: #000000;
        transition: all 400ms;
        transform: translate(0, 0);

        z-index: 99999;

        &:after {
            content: " ";
            position: absolute;
            width: 11px;
            height: 11px;
            right: 0;
            top: 3px;
            background-color: #000000;
            border-radius: 100%;
        }

        &.menu-open { 
            color: #FFFFFF;

            &:after {
                background-color: #FFFFFF;
            }
        }

        &.hidemenu {
            transform: translate(0, -100px);
        }
    }

    nav.nav-mobile {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;

        background: #000000;

        position: fixed;
        width: 100%;
        height: 100%;

        opacity: 0;
        pointer-events: none;

        transition: all 400ms;

        z-index: 999;

        &.menu-show {
            opacity: 1;
            pointer-events: all;
        }

        ul {
            margin: 0;
            padding: 0;
            width: 100%;
            height: 100%;
            max-height: 60%;

            display: flex;
            justify-content: space-around;
            flex-direction: column;
            align-items: center;

            li {
                margin: 0;
                padding: 0;

                a {
                    color: #FFF;
                    font-family: 'Nunito Sans', sans-serif;
                    font-weight: 900;
                    font-size: 28px;
                }
            }
        }
    }


    #content {
    
        h2 {
            font-size: 28px;
            margin-bottom: 30px;
        }
    
        h3 {
            font-size: 22px;
        }
    
        h4 {
            font-size: 20px;
        }
    
        p {
            margin-bottom: 15px;
        }
    }
    
    .section-wrap {
        min-height: 0;
    }

    .section-title-mobile {
        display: block;
        width: 100%;
        text-align: center;
        padding: 0 0 50px 0;
        position: relative;
        margin: 0 0 60px 0;

        &.line-under:after {
            content: " ";
            position: absolute;
            left: 50%;
            bottom: 0;
            width: 1px;
            height: 40px;
            background: #000000;
        }


        &.line-side{
            text-align: left;
            padding: 0 0 0 60px;
            margin: 60px 0 0 0;

            &:after {
                content: " ";
                position: absolute;
                left: 0;
                top: 50%;
                width: 40px;
                height: 1px;
                background: #000000;
            }
        }
    }


    #home {
        padding: 0;
    
        .front-image {
            display: none;
        }

        .front-image-mobile {
            display: block;
            padding-bottom: 80px;
        }
    }


        
    #about {
        margin: 0 0 90px 0;

        .about-inner {
            display: flex;
            flex-direction: column;

            padding: 0 35px;

            .about-half {
                width: 80%;
                margin-bottom: 50px;
            }

            .about-content {
                width: 100%;
                margin: 0;
            }
        }
    }




    #gallery {

        .gallery-inner {
            padding: 90px 40px;

            .gallery-item {

                &:nth-child(odd),
                &:nth-child(even) {
                    justify-content: center;
                    text-align: center;

                    p {
                        display: block;
                        position: relative;
                        padding-bottom: 56.25%;
                        height: 0;
                        overflow: hidden;
                        max-width: 100%;
                        height: auto;

                        iframe {
                            margin: 0 auto;
                            position: absolute;
                            top: 0;
                            left: 0;
                            right: 0;
                            width: 100%;
                            height: 100%;
                        }
                    }

                   
                }
            }
        }
    }



        
    #carousel {
        height: auto;
        flex-direction: column;

        .carousel-inner {
            padding: 0;

            .carousel-actions {
                display: none;
            }

            .carousel-left {
                display: none;
            }
        
            .teamcarousel {
                overflow: auto;
                padding: 0;
                flex-direction: column;

                .teamcarousel-item {
                    min-width: 100%;
                    margin: 35px 0;
                
                    .teamcarousel-item-img {
                        height: 215px;
                    }

                    .teamcarousel-item-body {
                        width: 100%;
                        margin: 0;
                        padding: 0;

                    }
                }
            }
        }
    }
}


@media (max-width: 600px) {

    
    

    #gallery {

        .gallery-inner {
            padding: 90px 40px;

            .gallery-item {

                &:nth-child(odd),
                &:nth-child(even) {

                    p {
                        padding-bottom: 40%;
                        height: 0;
                        overflow: hidden;
                        max-width: 100%;
                        height: auto;
                        margin: 40px 0;

                        iframe {
                            margin: 0 auto;
                            position: absolute;
                            top: 0;
                            left: 0;
                            right: 0;
                            width: 100%;
                            height: 100%;
                            max-width: 100%;
                            min-width: 0;
                        }
                    }
                }
            }
        }
    }




}